import React, { useState, useEffect } from "react";
import MultiLineChart from "./multilinechart";
import { Button, Input, Row, Col, InputNumber, Typography, message, Select } from "antd";
import axios from "axios";
import "./chartcontainer.css";

const { Title } = Typography;
const color = {};

const csrftoken = document.cookie.split('; ').find(row => row.startsWith('csrftoken')).split('=')[1];
axios.defaults.headers.common['X-CSRFToken'] = csrftoken;

const ChartContainer = () => {
    const [updatetime, setupdatetime] = useState(2);
    const [timecount, setimecount] = useState(0);
    const [items, setItems] = useState([]);
    const [curaddress, setCurAddress] = useState("");
    const [timeid, setTimeId] = useState(0);
    const [disaddress, setDisAddress] = useState("");
    const [hisdata, setHisData] = useState({});
    const [cData, setCData] = useState({ labels: [], datasets: [] });

    useEffect(() => {
        getAddresses();
    }, []);

    const getAddresses = async () => {
        try {
            const res = await axios.get("http://54.160.184.59/api/addresses/", {
                headers: { "Content-Type": "application/json" },
            });
            setItems(
                res.data.map((address, index) => ({
                    value: address.address,
                    label: address.address
                }))
            );
            if (res.data[0].address !== undefined)
                setCurAddress(res.data[0].address)
        } catch (error) {
            console.error(error);
            message.error("Failed to fetch addresses.");
        }
    };

    const handleAddressChange = (event) => {
        setCurAddress(event.target.value);
    };

    const onSubmit = () => {
        setDisAddress(curaddress);
        if (timeid) {
            clearInterval(timeid);
            setimecount(c => c + updatetime);

        }
        setTimeId(
            setInterval(() => {
                setimecount((prevCount) => prevCount + updatetime);
            }, updatetime * 1000)
        );
    };

    const onUpdateTimeChange = (value) => {
        setupdatetime(value);
    };

    const getData = async () => {
        const payload = { address: disaddress };
        try {
            const res = await axios.post(`http://54.160.184.59/api/addresses/`, payload);
            let pData = { ...hisdata };

            if (pData["time"] === undefined) {
                pData = { time: ["0"] };
            } else {
                pData.time.push("" + timecount);
                Object.keys(pData).forEach((address) => {
                    if (address !== "time") pData[address].push(0);
                });
            }

            Object.keys(res.data).forEach((address) => {
                const percent = res.data[address]["percentages"][0];
                const timesarray = pData.time;
                let dataarray = pData[address];
                if (dataarray === undefined && timesarray.length !== 1) {
                    dataarray = [];
                    for (let i = 0; i < timesarray.length; i++) dataarray.push(0);
                } else {
                    if (timesarray.length === 1) dataarray = [];
                    dataarray[timesarray.length - 1] = percent;
                }
                pData[address] = dataarray;
            });

            setHisData(pData);

            const datasets = [];
            for (const address in hisdata) {
                if (address !== "time")
                    datasets.push({
                        label: address,
                        data: hisdata[address],
                        fill: false,
                        borderColor: getborderColor(address),
                        tension: 0.1,
                    });
            }

            const chartdata = {
                labels: hisdata.time,
                datasets,
            };
            setCData(chartdata);
        } catch (error) {
            console.error(error);
            message.error("Failed to fetch data.");
        }
    };

    const changeAddress = (e) => {
        setCurAddress(e);
    };

    const getborderColor = (username) => {
        if (color[username] === undefined)
            color[username] = `rgb(${Math.random() * 255}, ${Math.random() * 255}, ${Math.random() * 255})`;
        return color[username];
    };

    useEffect(() => {
        if (timecount)
            getData();
    }, [curaddress, timecount]);

    return (
        <div className="ChartContainer" style={{ padding: "20px" }}>
            <Row justify="center" style={{ marginBottom: "20px" }}>
                <Title level={3} style={{ fontSize: "70px" }}>
                    Tracked Tokens
                </Title>
            </Row>
            <Row gutter={[16, 16]} align="middle">
                <Col span={3}>
                    <p style={{ fontSize: "20px", fontWeight: "bold" }}>Address:</p>
                </Col>
                <Col span={9}>
                    <Input
                        placeholder="Enter address"
                        value={curaddress}
                        onChange={handleAddressChange}
                        style={{ fontSize: "25px" }}
                    />
                </Col>
                <Col span={3}>
                    <p style={{ fontSize: "20px", fontWeight: "bold" }}>Update Time (s):</p>
                </Col>
                <Col span={3}>
                    <InputNumber
                        min={1}
                        value={updatetime}
                        onChange={onUpdateTimeChange}
                        style={{ width: "100%", fontSize: "25px" }}
                    />
                </Col>
                <Col span={3}>
                    <Button
                        type="primary"
                        onClick={onSubmit}
                        style={{ fontSize: "20px", padding: "25px 30px" }}
                    >
                        Submit
                    </Button>
                </Col>
            </Row>
            <Row justify="center" style={{ marginTop: "20px" }}>
                <Col>

                    <Select
                        size={'large'}
                        defaultValue={curaddress}
                        onChange={changeAddress}
                        style={{
                            width: '50vw',
                        }}
                        options={items}
                    />
                </Col>
            </Row>
            <div style={{ marginTop: "20px" }}>
                <MultiLineChart data={cData} address={disaddress} />
            </div>
        </div>
    );
};

export default ChartContainer;
